	module.exports = {"data":{"challengeNode":{"title":"Change the Color of an SVG Element","description":["所有组都在正确的位置上了，但是它们都是一样的黑色。SVG 可以改变组的颜色。","在 SVG 中，<code>rect</code>图形用<code>fill</code>属性着色，它支持十六进制代码、颜色名称、rgb 值以及更复杂的选项，比如渐变和透明。","<hr>","添加<code>attr()</code>方法，将所有组的 \"fill\" 设置为 \"navy\"。"],"challengeType":0,"fields":{"slug":"/data-visualization/data-visualization-with-d3/change-the-color-of-an-svg-element","blockName":"Data Visualization with D3","tests":[{"text":"所有组的<code>fill</code>颜色都应该是 navy。","testString":"assert($('rect').css('fill') == \"rgb(0, 0, 128)\", '所有组的<code>fill</code>颜色都应该是 navy。');"}]},"required":[{"link":null,"raw":null,"src":"https://cdnjs.cloudflare.com/ajax/libs/d3/4.3.0/d3.min.js"}],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<body>\n  <script>\n    const dataset = [12, 31, 22, 17, 25, 18, 29, 14, 9];\n    \n    const w = 500;\n    const h = 100;\n    \n    const svg = d3.select(\"body\")\n                  .append(\"svg\")\n                  .attr(\"width\", w)\n                  .attr(\"height\", h);\n    \n    svg.selectAll(\"rect\")\n       .data(dataset)\n       .enter()\n       .append(\"rect\")\n       .attr(\"x\", (d, i) => i * 30)\n       .attr(\"y\", (d, i) => h - 3 * d)\n       .attr(\"width\", 25)\n       .attr(\"height\", (d, i) => 3 * d)\n       // 在下面添加你的代码\n       \n       \n       \n       // 在上面添加你的代码\n  </script>\n</body>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":null,"raw":null,"src":"https://cdnjs.cloudflare.com/ajax/libs/d3/4.3.0/d3.min.js"}],"nextChallengePath":"/data-visualization/data-visualization-with-d3/add-labels-to-d3-elements","id":"587d7fa9367417b2b2512bd1"},"slug":"/data-visualization/data-visualization-with-d3/change-the-color-of-an-svg-element"}}

